/*important стили перекрывают библиотеку MUI */

.small-input div input {
    width: 3em;
    padding: 5px !important;
    text-align: center;
}

.form-view {
    display: flex;
    flex-direction: column;
    width: 60vw;
    padding: 20px;
    margin: 0 auto;
}

.form-view-half {
    width: 26vw;
}

.icon-button {
    color: green !important;
    margin-top: -8px !important;
    padding: 10px !important;
}

.icon-square-button {
    padding: 2px 10px !important;
    min-width: 0 !important;
    margin-right: 2px !important;
}

.icon-square-button span span {
    margin: 0 !important;
}

.h0 {
    color: #6C6C6C;
    font-size: 14px;
    margin-top: 6px;
}

.preset-button {
    background-color: #04BDFF !important;
    padding: 2px 6px !important;
    color: #fff !important;
    font-weight: bold !important;
    font-size: 12px !important;
    margin-right: 2px !important;
}

.flex-row-container {
    display: flex;
    justify-content: space-between;
}

.form-buttons-container {
    margin-top: 15px;
    display: flex;
    justify-content: right;
}

.positive-button {
    background-color: #6AA957 !important;
    margin-right: 4px !important;
    color: #fff !important;
    font-weight: bold !important;
}

/* LOADER */
.lds-dual-ring {
    display: inline-block;
    width: 64px;
    height: 64px;
}
.lds-dual-ring:after {
    content: ' ';
    display: block;
    width: 46px;
    height: 46px;
    margin: 1px;
    border-radius: 50%;
    border: 5px solid #04BDFF;
    border-color: #04BDFF transparent #04BDFF transparent;
    animation: lds-dual-ring 1.2s linear infinite;
}
@keyframes lds-dual-ring {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}